"use scrict";
import Options from "@/components/Options/index.vue";
import {NOTICE_SELECT_ITEM} from "@/config/router.js";

export default {
	data() {
		return {
			options: {}
		}
	},
	onLoad(options) {
		this.options = options;
	},
	methods: {
		linkToMultipleSelect: function ({type}) {
			this.$toPage(NOTICE_SELECT_ITEM, {
				type,
				...this.options
			})
		}
	},
	components: {
		Options
	}
}
